<template>
  <div class="app">
    <!-- header -->
    <app-header :poiInfo="poiInfo"></app-header>
    <!-- nav -->
    <app-nav :commentNum="commentNum"></app-nav>
    <!-- content -->
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script type="text/ecmascript-6">
import Header from "./components/header//Header.vue";
import Nav from "./components/nav/Nav.vue";
export default {
  data() {
    return {
      poiInfo: {},
      commentNum: 0
    };
  },
  created() {
    //
    fetch("https://wd5505049346cgoufk.wilddogio.com/goods.json")
      .then(res => res.json())
      .then(res => {
        if (res.code == 0) {
          this.poiInfo = res.data.poi_info;
        }
      });
    // rating
    fetch("https://wd5505049346cgoufk.wilddogio.com/ratings.json")
      .then(res => res.json())
      .then(res => {
        if (res.code == 0) {
          this.commentNum = res.data.comment_num;
        }
      });
  },
  components: {
    "app-header": Header,
    "app-nav": Nav
  }
};
</script>

<style scoped lang="stylus">
</style>
