<template>
  <div class="goods">
    <!--  -->
    <div class="menu-wrapper" ref="menuScroll">
      <ul>
        <!--  -->
        <li class="menu-item" @click="selectMenu(0)" :class="{'current':currentIndex === 0}">
          <p class="text">
            <img class="icon" :src="container.tag_icon" v-if="container.tag_icon">
            {{container.tag_name}}
          </p>
        </li>

        <li
          class="menu-item"
          v-for="(item, index) in goods"
          :key="index"
          :class="{'current':currentIndex === index + 1}"
          @click="selectMenu(index +1)"
        >
          <p class="text">
            <img class="icon" :src="item.icon" v-if="item.icon">
            {{item.name}}
          </p>
          <i class="num" v-show="calculateCount(item.spus)">{{calculateCount(item.spus)}}</i>
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="foods-wrapper" ref="foodScroll">
      <ul>
        <!--  -->
        <li class="container-list food-list-hook">
          <div v-for="(item, index) in container.operation_source_list" :key="index">
            <img :src="item.pic_url">
          </div>
        </li>
        <!--  -->
        <li class="food-list food-list-hook" v-for="(item, index) in goods" :key="index">
          <h3 class="title">{{item.name}}</h3>
          <!--  -->
          <ul>
            <li
              v-for="(food,index) in item.spus"
              :key="index"
              class="food-item"
              @click="showDetail(food)"
            >
              <div class="icon" :style="head_bg(food.picture)"></div>
              <div class="content">
                <h3 class="name">{{food.name}}</h3>
                <p class="desc" v-if="food.description">{{food.description}}</p>
                <div class="extra">
                  <span class="saled">{{food.month_saled_content}}</span>
                  <span class="praise">{{food.praise_content}}</span>
                </div>
                <img class="product" :src="food.product_label_picture">
                <p class="price">
                  <span class="text">{{food.min_price}}</span>
                  <span class="unit">/{{food.unit}}</span>
                </p>
              </div>
              <div class="cartcontrol-wrapper">
                <app-cart-control :food="food"></app-cart-control>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!--  -->
    <app-shopcart :poiInfo="poiInfo" :selectFoods="selectFoods"></app-shopcart>
    <!-- -->
    <app-product-detail :food="selectFood" ref="foodView"></app-product-detail>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from "better-scroll";
import Shopcart from "../shopcart/shopcart";
import Cartcontrol from "../cartcontrol/cartcontrol";
import ProductDetail from "../productDetail/productDetail";
export default {
  data() {
    return {
      container: {},
      goods: [],
      listHeight: [],
      menuScroll: {},
      foodScroll: {},
      scrollY: 0,
      poiInfo: {},
      selectFood: {}
    };
  },
  created() {
    //
    fetch("https://wd5505049346cgoufk.wilddogio.com/goods.json")
      .then(res => res.json())
      .then(res => {
        if (res.code == 0) {
          this.container = res.data.container_operation_source;
          this.goods = res.data.food_spu_tags;
          this.poiInfo = res.data.poi_info;
          // console.log(this.poiInfo);

          //don
          this.$nextTick(() => {
            // 
            this.initScroll();

            // 
            this.calculateH();
            // 
            // 
            // 
          });
        }
      });
  },
  methods: {
    head_bg(imgName) {
      return "background-image: url(" + imgName + ");";
    },
    initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuScroll, {
        click: true
      });
      this.foodScroll = new BScroll(this.$refs.foodScroll, {
        probeType: 3,
        // scrollY: true,
        click: true,
        dblclick: {
          delay: 300
        },
        bounce: {
          top: true,
          bottom: true,
          left: true,
          right: true
        }
      });
      // foodScroll 
      this.foodScroll.on("scroll", pos => {
        // console.log(pos.y);
        this.scrollY = Math.abs(Math.round(pos.y));
        // console.log(this.currentIndex);

        // console.log(this.scrollY);
      });
    },
    calculateH() {
      let foodlist = this.$refs.foodScroll.getElementsByClassName(
        "food-list-hook"
      );
      // console.log(foodlist);
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodlist.length; i++) {
        const item = foodlist[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
      // console.log(this.listHeight);
    },
    selectMenu(index) {
      // console.log(index);
      let foodlist = this.$refs.foodScroll.getElementsByClassName(
        "food-list-hook"
      );
      const element = foodlist[index];
      this.foodScroll.scrollToElement(element, 100);
    },
    calculateCount(spus) {
      let num = 0;
      spus.forEach(food => {
        if (food.count > 0) {
          num += food.count;
        }
      });
      return num;
    },
    showDetail(food) {
      this.selectFood = food;
      this.$refs.foodView.showView();
    }
  },

  components: {
    "app-shopcart": Shopcart,
    "app-cart-control": Cartcontrol,
    "app-product-detail": ProductDetail
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let heigth1 = this.listHeight[i];
        let heigth2 = this.listHeight[i + 1];
        // 
        if (!heigth2 || (this.scrollY < heigth2 && this.scrollY >= heigth1)) {
          return i;
        }
      }
      return 0;
    },
    selectFoods() {
      let foods = [];
      this.goods.forEach(myfoods => {
        myfoods.spus.forEach(food => {
          if (food.count > 0) {
            foods.push(food);
          }
        });
      });
      return foods;
    }
  }
};
</script>

